* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

.header {
  z-index: 100;
  width: 100%;
  height: 60px;
  background-color: #6495ed;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  padding: 8px 0;
  display: flex;
  position: fixed;
  top: 0;
  box-shadow: 0 2px 5px -1px rgba(50, 50, 93, .25), 0 1px 3px -1px rgba(0, 0, 0, .3);
}

.input {
  width: 264px;
  height: 30px;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 4px;
  box-shadow: inset 3px 3px 6px #ccdbe8, inset -3px -3px 6px 1px rgba(255, 255, 255, .5);
}

.search-btn {
  cursor: pointer;
  height: 30px;
  background-color: #eee;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 4px;
  box-shadow: 0 1px 2px rgba(60, 64, 67, .3), 0 1px 3px 1px rgba(60, 64, 67, .15);
}

.gallery {
  flex-wrap: wrap;
  align-items: stretch;
  gap: 15px;
  padding: 70px 10px 0;
  display: flex;
  position: relative;
}

.photo-card {
  border: 1px solid gray;
  flex-basis: 100%;
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

@media only screen and (min-width: 560px) {
  .photo-card {
    flex-basis: calc(50% - 7.5px);
  }
}

@media only screen and (min-width: 800px) {
  .photo-card {
    flex-basis: calc(33.3333% - 10px);
  }
}

@media only screen and (min-width: 1100px) {
  .photo-card {
    flex-basis: calc(25% - 11.25px);
  }
}

.photo-link {
  text-decoration: none;
  display: block;
}

.photo-link:hover {
  cursor: zoom-in;
}

.photo-img {
  width: 100%;
  aspect-ratio: 4 / 3;
  object-fit: cover;
  display: block;
}

.info {
  justify-content: space-between;
  padding: 4px;
  display: flex;
}

.info-item {
  text-align: center;
  margin: 8px 0;
}

.info-item b {
  display: block;
}

.load-more {
  visibility: hidden;
  margin: 15px auto;
  display: block;
}

